
import XHeader from 'vux/src/components/x-header/index.vue'

import backTop from '@/components/BackTop/backTop'
import showMore from '@/components/ShowMore/showMore' 
export default {
components: {
XHeader,
backTop,
showMore
},
data() {
return {
type: '',
cbh: '',
tm: '',
list: [],
pagedata: {


}
}
},
mounted() {
this.getTypeCbh(); 
this.initdata();
},
methods: {
initdata() {

this.$ajax.post('', {
'MSH.1': 'qtylfw',
'MSH.2': 'qryDetail',
"CBH": this.cbh,
"TM": this.tm,
"TYPE": this.type,
'CHZBM': this.$root.loginInfo.CHZBM,
'token':this.$root.loginInfo.token,
}).then(data => {
if (typeof data != 'undefined') {

this.pagedata = data;
} else {
this.pagedata = {}
}
})
},
getTypeCbh() {
this.type = this.$route.params.TYPE
this.cbh = this.$route.params.CBH
this.tm = this.$route.params.TM
console.log(this.$route.params)
}
}
}



